:root {
	--red: #b22222;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.nav {
	grid-row: 2;
	grid-column: 1 / -1;
	overflow: auto;
}
.nav.hidden {
	max-height: 0;
	overflow: hidden;
	-webkit-transform: rotateX(90deg);
	        transform: rotateX(90deg);
	transition: all 0.5s;
	grid-row: unset;
	grid-column: unset;
}

.nav ul {
	display: grid;
	grid-auto-flow: column;
	grid-gap: 10px;
	grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
	margin: 0;
	padding: 0;
	list-style: none;
	-webkit-align-content: center;
	        align-content: center;
}

.nav a {
	position: relative;
	display: block;
	text-decoration: none;
	color: inherit;
	padding: 10px;
	text-align: center;
	text-transform: uppercase;
	font-size: 20px;
}
.nav a:hover {
	/* background: linear-gradient(to bottom, transparent, var(--red)); */
}

.nav a.active:after,
.nav a:hover:after {
	display: block;
	content: '';
	height: 2px;
	width: calc(100% - 6px);
	position: absolute;
	left: 3px;
	bottom: 3px;
	background: var(--red);
}

.icon-button {
	box-sizing: border-box;
	padding: 15px;
	border: none;
	background: transparent;
	justify-self: start;
	-webkit-align-self: center;
	        align-self: center;
	margin: 10px;
	border-radius: 50%;
	color: inherit;
}
.icon-button:hover {
	background: rgba(0, 0, 0, 0.1);
}

.header.sm,
.header.md,
.header.l {
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 1fr auto 1fr;
	grid-template-rows: 70px;
	grid-template-rows: var(--rowHeight, 70px);
	-webkit-justify-content: center;
	        justify-content: center;
	max-height: 400px;
	width: 100%;
	height: -webkit-min-content;
	height: -moz-min-content;
	height: min-content;
	box-sizing: border-box;
}
.header.md {
	--rowHeight: 120px;
}
.header.l {
	--rowHeight: 170px;
}
.header__logo {
	align-self: center;
	grid-column: 2 / span 1;
}
.header__logo.sm,
.header__logo.md,
.header__logo.l {
	height: 50px;
	height: var(--logoHeight, 50px);
	width: 100%;
}
.header__logo.md {
	--logoHeight: 100px;
}
.header__logo.l {
	--logoHeight: 150px;
}

.article-list {
	display: grid;
	overflow: auto;
	height: 100%;
}

.article-list-item {
	padding: 10px;
	display: grid;
	grid-auto-flow: dense;
	border-bottom: 1px solid var(--red);
	grid-gap: 10px;
	place-items: start start;
	-webkit-align-content: start;
	        align-content: start;
	/* grid-template-areas:
		'img title title'
    'img description description'; */
	grid-template-columns: auto 1fr;
}

.article-list-item__img-container {
	display: grid;
	justify-self: center;
	align-self: center;
	grid-row: 1 / span 2;
	grid-column: 1;
}
.article-list-item__img {
	height: 128px;
	grid-row: 1 / span 2;
	grid-column: 1;
}

.article-list-item__title {
	/* grid-area: title; */
	text-align: left;
}
.article-list-item__description {
	/* grid-area: description; */
	text-align: left;
}

.App {
	text-align: center;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	height: 100vh;
	overflow: hidden;
}

.App__content {
	position: relative;
	height: calc(100vh - (113px));
	height: calc(100vh - (var(--headerHeight, 113px)));
	-webkit-flex: 1 1;
	        flex: 1 1;
	overflow: auto;
}

